.remoteWorkBlock {
    background-color: #0c0c0c;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
}
.contentBlock {
    margin: 0 auto;
}
.contentRemoteWork {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;

}
.title {
    text-align: center;
    position: relative;
    bottom: 50px;
    color: #BDECC5;
}
.button {
    width: 100%;
    margin: 0 auto;
    position: relative ;
}
.href {
    position: absolute;
    top: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
    background-color: white;
}
.button:hover {
    background-color: #49f8a1;
    color: #0c0c0c;
    border-radius: 20px;
}