.cardBlock {
    display: flex;
    justify-content: center;
    align-items: center;
}
.card {
    position: relative;
}

.card .face{
    width: 400px;
    height: 150px;
    transition: 0.5s;
}

.card .face.face1{
    position: relative;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    transform: translateY(50px);
    background: #101010;
    box-shadow: 1px 0px 42px -12px rgb(106, 247, 151);
}

.card:hover .face.face1{
    transform: translateY(0);
}

.card .face.face1 .content{
    opacity: 1;
    transition: 0.5s;
}

.card:hover .face.face1 .content{
    opacity: 1;
}

.card .face.face1 .content img{
    max-width: 100px;
}

.card .face.face1 .content h3{
    margin: 10px 0 10px;
    padding: 0;
    text-align: center;
}

.card .face.face2{
    position: relative;
    background: #101010;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
    /*box-shadow: 0 20px 50px rgba(0, 0, 0, 0.8);*/
    transform: translateY(-100px);
    border-radius: 10px;
}

.card:hover .face.face2{
    transform: translateY(0);
}

.card .face.face2 .content p{
    margin: 0;
    padding: 0;
}

.card .face.face2 .content a{
    margin: 15px 0 0;
    display:  inline-block;
    text-decoration: none;
    font-weight: 900;
    color: #333;
    padding: 5px;
    border: 1px solid #333;
}

.card .face.face2 .content a:hover{
    background: #333;
    color: #fff;
}
@media (max-width: 767px) {
    .card:focus .face.face1{
        transform: translateY(0);

    }
    .card:focus .face.face1 .content{
        opacity: 1;
    }
    .card:focus .face.face2{
        transform: translateY(0);
    }

}

.title {
    font-size: 35px;
    color: #BDECC5
}
.info {
    width: 100%;
    margin:0 auto;
    text-align: center;
    color: #BDECC5
}
@media (min-width: 1200px) {

}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
    .card .face{
        width: 350px;
        height: 150px;
        transition: 0.5s;
    }
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {

}

@media (max-width: 480px) {
    .card .face{
        width: 300px;
        height: 150px;
        transition: 0.5s;
    }
}

/* Iphone 5 */
@media (max-width: 380px) {
    .card .face{
        width: 250px;
        height: 150px;
        transition: 0.5s;
        font-size: 70%;
    }
}